<template>
  <div>
    <el-card>
      <div slot="header" class="clearfix">
        <span>工站编号 : {{ CurrentUserInfo.work_id }}</span>
        <span class="ml20">工站名称 : {{ workStationName }}</span>
        <span class="ml20">工站类型 : {{ CurrentUserInfo.work_type }}</span>
      </div>
      <el-form
        class=""
        style="padding: 0 60px 0 20px; width: 650px"
        ref="formRef"
        :model="form"
        :rules="formRules"
        label-width="100px"
      >
        <!-- <el-form-item label="生产计划" prop="product_plan_code">
          <el-select
            v-model="form.product_plan_code"
            style="width: 100%"
            placeholder="请选择"
          >
            <el-option
              v-for="(item, index) in ProductPlanCodeNameList"
              :key="index + ' search'"
              :label="item.product_plan_name"
              :value="item.product_plan_code"
            ></el-option>
          </el-select>
        </el-form-item> -->
        <el-form-item
          prop="finished_product_code"
          label="产品编号"
          :rules="[{ required: true, message: '产品编号不能为空' }]"
        >
          <el-input
            ref="firstInput"
            v-model="form.finished_product_code"
            @input="inputChange"
          ></el-input>
        </el-form-item>

        <div
          v-for="(item, index) in form.response_datas"
          :key="index + ' onlyItem'"
        >
          <el-form-item
            :prop="'response_datas.' + index + '.matter_id'"
            :rules="[
              {
                required: true,
                message: '物料编码不能为空',
                trigger: 'blur',
              },
              {
                pattern: item.rule,
                message: '编码需要包含' + item.rule,
                trigger: 'blur',
              },
            ]"
            :label="item.matter_name"
          >
            <el-input
              :ref="'matterInput' + index"
              v-model="item.matter_id"
              @input="
                nextFocus(
                  'matterInput' + (index + 1),
                  index,
                  item.matter_code,
                  $event
                )
              "
            ></el-input>
          </el-form-item>
        </div>

        <el-form-item label="检验结果" prop="test_result">
          <el-select
            v-model="form.test_result"
            placeholder="请选择"
            style="width: 100%"
          >
            <el-option label="合格" value="合格"></el-option>
            <el-option label="不合格" value="不合格"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item
          label="备注"
          prop="description"
          v-if="form.test_result == '不合格'"
        >
          <el-input type="textarea" v-model="form.description"></el-input>
        </el-form-item>
      </el-form>

      <div>
        <el-button @click="resetForm">重 置</el-button>
        <el-button type="primary" @click="dialogSave">确 定</el-button>
      </div>
    </el-card>
  </div>
</template>

<script>
import { baseManagement } from "@/request/api";
import { mapState, mapActions } from "vuex";

export default {
  name: "MissStation",
  data() {
    return {
      timeout: "",
      QualifiedFlag: false,
      // 产品过站弹出层
      form: {
        product_plan_code: "",
        finished_product_code: "",
        response_datas: [],
        test_result: "合格",
      },
      formBase: {
        product_plan_code: "",
        finished_product_code: "",
        response_datas: [],
        test_result: "合格",
      },
      formRules: {
        test_result: [
          { required: true, message: "请选择检验结果", trigger: "change" },
        ],
      },
      response_datasRules: {
        matter_id: [
          {
            required: true,
            message: "数量不能为空",
            trigger: "blur",
          },
        ],
      },
    };
  },
  computed: {
    ...mapState([
      "ProductPlanCodeNameList",
      "MatterCodeNameList",
      "BomMatterCodeNameList",
      "WorkCodeNameList",
      "CurrentUserInfo",
    ]),
  },
  methods: {
    ...mapActions([
      "getLogisticProductPlanCodeName",
      "getLogisticMatterNameCode",
      "getLogisticBomMatterCodeName",
      "getLogisticWorkCodeName",
    ]),
    inputChange(value) {
      let inputVal = value;

      if (this.timeout) {
        clearTimeout(this.timeout);
      }
      this.timeout = setTimeout(() => {
        if (this.form.finished_product_code == inputVal) {
          if (this.form.finished_product_code) {
            let params = {
              finished_product_code: this.form.finished_product_code,
            };
            baseManagement
              .getLogisticQualifiedMatterCode(params)
              .then((res) => {
                console.log("寻找此工站的上一站的物料是否合格接口： 查询", res);
                if (res.code == 0) {
                  this.QualifiedFlag = true;
                } else {
                  this.$message({
                    message: res.message,
                    type: "error",
                  });
                }
              });
          }
        }
      }, 500);
    },

    nextFocus(nextRef, index, matter_code, value) {
      let inputVal = value;
      let thisInputVal = this.form.response_datas[index].matter_id;

      if (this.timeout) {
        clearTimeout(this.timeout);
      }
    
      this.timeout = setTimeout(() => {
        if (index < this.form.response_datas.length - 1) {
          this.$nextTick(() => {
            this.$refs[nextRef][0].focus();
          });
        } else {
        }
      }, 500);
    },

    // 通过工站获取BOM产品物料：
    async getLogisticWorkStationGetData() {
      // 产品　物料code和名称接口 查询
      await this.getLogisticBomMatterCodeName();
      baseManagement.getLogisticWorkStationGetData().then((res) => {
        console.log("通过工站获取BOM产品物料： 查询", res);
        console.log(
          "产品　物料code和名称接口 查询 BomMatterCodeNameList",
          this.BomMatterCodeNameList
        );
        if (res.code == 0) {
          let resData = res.data[0].response_datas;
          let newResData = [];

          resData.forEach((resDataItem, index) => {
            this.BomMatterCodeNameList.forEach((BomMatterCodeNameItem) => {
              if (
                resDataItem.matter_code == BomMatterCodeNameItem.matter_code
              ) {
                resDataItem.matter_name = BomMatterCodeNameItem.matter_name;
                resDataItem.rule = BomMatterCodeNameItem.rule;
              }
            });
          });
          for (let index = 0; index < resData.length; index++) {
            for (
              let itemIndex = 0;
              itemIndex < resData[index].install_number;
              itemIndex++
            ) {
              newResData.push({
                matter_name: resData[index].matter_name,
                matter_code: resData[index].matter_code,
                matter_id: resData[index].matter_id,
                rule: resData[index].rule,
              });
            }
          }
          this.form.response_datas = newResData;
        } else {
          this.$message({
            message: res.message,
            type: "error",
          });
        }
      });
    },

    // 产品过站 新增
    postLogisticProductTransitInfo(params) {
      baseManagement.postLogisticProductTransitInfo(params).then((res) => {
        console.log("产品过站 新增", res);
        if (res.code == 0) {
          this.resetForm();
          this.$message({
            message: res.message,
            type: "success",
          });
        } else {
          this.$message({
            message: res.message,
            type: "error",
          });
        }
      });
    },

    // 重置按钮
    resetForm() {
      this.form = JSON.parse(JSON.stringify(this.formBase));
      this.getLogisticWorkStationGetData();

      this.$nextTick(() => {
        this.$refs.formRef.clearValidate();
      });
    },

    // 确认按钮
    dialogSave() {
      this.$refs.formRef.validate((valid) => {
        if (!valid) {
          this.$message({
            message: "请修改正确的数据格式！",
            type: "warning",
          });
          return;
        } else {
          //验证通过调用保存接口
          console.log("产品过站", this.form);
          if (this.QualifiedFlag) {
            this.postLogisticProductTransitInfo(this.form);
          } else {
            this.$message({
              message: "该产品上一工站不合格",
              type: "error",
            });
          }
        }
      });
    },
  },
  mounted() {
    // 通过工站获取BOM产品物料：
    this.getLogisticWorkStationGetData();
    // 工站内码和工站名称接口： 查询
    this.getLogisticWorkCodeName();
    this.WorkCodeNameList.forEach((CodeNameListItem) => {
      if (this.CurrentUserInfo.work_id == CodeNameListItem.work_id) {
        this.workStationName = CodeNameListItem.work_name;
      }
    });

    this.$nextTick(() => {
      this.$refs.firstInput.focus();
    });
  },
};
</script>

